
import { defineComponent, onMounted, ref } from 'vue'
import gsap from 'gsap'
import TextPlugin from 'gsap/TextPlugin'

export default defineComponent({
  setup () {
    const infoArr = ref([
      {
        link: '',
        text: 'Eric Chiu',
        icon: 'address-card',
        type: 'far'
      },
      {
        link: 'mailto:eric840906@hotmail.com.tw',
        text: 'eric840906@hotmail.com.tw',
        icon: 'envelope',
        type: 'far'
      },
      {
        link: 'https://hackmd.io/@c04lOMyVQZSN-SPA2h-MeA',
        text: 'Hackmd notes',
        icon: 'pencil-alt',
        type: 'fas'
      }
    ])
    const linkBtnArr = ref([
      {
        link: 'https://github.com/eric840906',
        icon: 'github-alt'
      },
      {
        link: 'https://codepen.io/dyhebcro',
        icon: 'codepen'
      },
      {
        link: 'https://www.linkedin.com/in/eric-chiu-911a5a174/',
        icon: 'linkedin-in'
      }
    ])
    const beforeEnter = (el: HTMLElement) => {
      el.style.opacity = '0'
      el.style.transform = 'translateY(10px)'
    }
    const enter = (el: HTMLElement) => {
      if (!el.dataset.index) return
      console.log(el)
      gsap.to(el, {
        opacity: 1,
        y: 0,
        duration: 0.8,
        delay: 0.2 * +el.dataset.index
      })
    }
    gsap.registerPlugin(TextPlugin)
    const tl = gsap.timeline()
    onMounted(() => {
      tl.to('#my-intro', { duration: 5, text: 'HI! My name is Eric,<br> a front-end developer.' }, '1')
    })
    return {
      infoArr,
      linkBtnArr,
      beforeEnter,
      enter
    }
  }
})
